import React, { useEffect, useState } from "react";
import { useTranslation } from "react-i18next";
import { useHistory, useLocation } from "react-router-dom";
import { toast } from "react-toastify";
import Select from "react-select";
import {
  FaBook,
  FaCalendar,
  FaCode,
  FaColumns,
  FaExchangeAlt,
  FaPlus,
  FaRemoveFormat,
  FaSync,
  FaTextWidth,
  FaTrash,
} from "react-icons/fa";
import {
  Button,
  Form,
  FormGroup,
  FormText,
  Input,
  InputGroup,
  InputGroupAddon,
  InputGroupText,
  Label,
} from "reactstrap";
import queryString from "query-string";
import { Formik } from "formik";
import * as Yup from "yup";

import "./TopicEdit.css";

import { RoutesString } from "pages/routesString";
import topicAPI from "api/common/topic";
import syllabusAPI from "api/common/syllabus";
import teachingPlanTypeAPI from "api/common/teaching-plan-type";
import teachingActivityAPI from "api/common/teaching-activity";
import evaluationActivityAPI from "api/common/evaluation-activity";
import subjectOutputStandardAPI from "api/common/subject-output-standard";
import Breadcrumbs from "components/admin/Breadcrumbs/Breadcrumbs";
import AsteriskIcon from "components/common/AsteriskIcon/AsteriskIcon";
import Notification from "components/common/Notification/Notification";
import LoadingBar from "components/common/LoadingBar/LoadingBar";
import PageHeader from "./components/PageHeader/PageHeader";
import i18n from "i18n/i18n";

export default function TopicEdit() {
  const { t } = useTranslation();

  const history = useHistory();

  const location = useLocation();

  const [id, setId] = useState(null);
  const [isUpdatePage, setIsUpdatePage] = useState(false);
  const [initialValues, setInitialValues] = useState({
    ma: "",
    ten: "",
    tuan: "",
    idSyllabus: "",
    idLKHGD: "",
    hoatDongDayHoc: [],
    hoatDongDanhGia: [],
    chuanDauRaMonHoc: [],
  });
  const [notification, setNotification] = useState({
    color: "danger",
    isOpen: false,
    messages: [],
  });
  const [isLoading, setIsLoading] = useState(false);
  const [syllabuses, setSyllabuses] = useState([]);
  const [teachingPlanTypes, setTeachingPlanTypes] = useState([]);
  const [teachingActivities, setTeachingActivities] = useState([]);
  const [evaluationActivities, setEvaluationActivities] = useState([]);
  const [subjectOutputStandards, setSubjectOutputStandards] = useState([]);

  useEffect(async () => {
    const parsed = queryString.parse(window.location.search);
    if (parsed.id) {
      setId(parsed.id);
      setIsUpdatePage(true);
      updateForm(parsed.id);
    }
    setIsLoading(true);
    try {
      const syllabusesRes = await syllabusAPI.getAll();
      setSyllabuses(syllabusesRes.contents);
      const teachingPlanTypesRes = await teachingPlanTypeAPI.getAll();
      setTeachingPlanTypes(teachingPlanTypesRes.contents);
      if (!parsed.id) {
        setInitialValues({
          ...initialValues,
          idSyllabus: syllabusesRes.contents[0].id,
          idLKHGD: teachingPlanTypesRes.contents[0].id,
        });
      }
      const teachingActivitiesRes = await teachingActivityAPI.getAll();
      const teachingActivitiesOptions = teachingActivitiesRes.contents.map(
        (item) => {
          return { value: item.id, label: item.ten };
        }
      );
      setTeachingActivities(teachingActivitiesOptions);
      const evaluationActivitiesRes = await evaluationActivityAPI.getAll();
      const evaluationActivitiesOptions = evaluationActivitiesRes.contents.map(
        (item) => {
          return { value: item.id, label: item.ten };
        }
      );
      setEvaluationActivities(evaluationActivitiesOptions);
      const subjectOutputStandardsRes = await subjectOutputStandardAPI.getAll();
      const subjectOutputStandardsOptions = subjectOutputStandardsRes.contents.map(
        (item) => {
          return { value: item.id, label: item.ma };
        }
      );
      setSubjectOutputStandards(subjectOutputStandardsOptions);
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
          toast.error(t("RESPONSE_MESSAGE403"));
        }
        else if (err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          toast.error(t("RESPONSE_MESSAGE500"));
        }
      }
    } finally {
      setIsLoading(false);
    }
  }, [location]);

  const updateForm = async function (id) {
    setIsLoading(true);
    try {
      const res = await topicAPI.getById(id);
      setInitialValues({
        ma: res.ma,
        ten: res.ten,
        tuan: res.tuan,
        idSyllabus: res.idSyllabus.id,
        idLKHGD: res.idLKHGD.id,
        hoatDongDayHoc: res.hoatDongDayHoc.map((item) => ({
          value: item.id,
          label: item.ten,
        })),
        hoatDongDanhGia: res.hoatDongDanhGia.map((item) => ({
          value: item.id,
          label: item.ten,
        })),
        chuanDauRaMonHoc: res.chuanDauRaMonHoc.map((item) => ({
          value: item.id,
          label: item.ma,
        })),
      });
    } catch (err) {
      if (err?.response) {
        if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
          toast.error(t("RESPONSE_MESSAGE403"));
        }
        else if (err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("RESPONSE_MESSAGE404"));
        } else {
          if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
            toast.error(t("ER_DUP_ENTRY"));
          } else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            toast.error(t("RESPONSE_MESSAGE500"));
          }
        }
      }
    } finally {
      setIsLoading(false);
    }
  };

  const handleEditOnClick = async function (values, isValid) {
    if (!isValid) {
      return;
    }
    if (isUpdatePage) {
      const body = {
        ...values,
        idSyllabus: parseInt(values.idSyllabus),
        idLKHGD: parseInt(values.idLKHGD),
        hoatDongDayHoc: values.hoatDongDayHoc.map((item) =>
          item.value.toString()
        ),
        hoatDongDanhGia: values.hoatDongDanhGia.map((item) =>
          item.value.toString()
        ),
        chuanDauRaMonHoc: values.chuanDauRaMonHoc.map((item) =>
          item.value.toString()
        ),
      };
      try {
        const res = await topicAPI.putById(id, body);
        if (res.status === 200) {
          setNotification({
            color: "success",
            isOpen: true,
            messages: [t("admin.UPDATE_SUCCESS")],
          });
          updateForm(id);
        }
      } catch (err) {
        if (err?.response) {
          if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
            toast.error(t("RESPONSE_MESSAGE403"));
          }
          else if (err?.response?.status === 401) {
            toast.error(t("RESPONSE_MESSAGE401"));
          } else if (err?.response?.status === 400) {
            toast.error(t("RESPONSE_MESSAGE400"));
          } else if (err?.response?.status === 409) {
            toast.error(t("RESPONSE_MESSAGE409"));
          } else if (err?.response?.status === 404) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
              toast.error(t("ER_DUP_ENTRY"));
            } else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
              toast.error(t("RESPONSE_MESSAGE404"));
            } else {
              toast.error(t("RESPONSE_MESSAGE500"));
            }
          }
        }
      }
    } else {
      const body = {
        ...values,
        idSyllabus: parseInt(values.idSyllabus),
        idLKHGD: parseInt(values.idLKHGD),
        hoatDongDayHoc: values.hoatDongDayHoc.map((item) =>
          item.value.toString()
        ),
        hoatDongDanhGia: values.hoatDongDanhGia.map((item) =>
          item.value.toString()
        ),
        chuanDauRaMonHoc: values.chuanDauRaMonHoc.map((item) =>
          item.value.toString()
        ),
      };
      try {
        const res = await topicAPI.post(body);
        if (res) {
          setNotification({
            color: "success",
            isOpen: true,
            messages: [t("admin.ADD_SUCCESS")],
          });
          history.push(`${RoutesString.ADMIN_TOPIC_EDIT}?id=${res.id}`);
        }
      } catch (err) {
        if (err?.response) {
          if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
            toast.error(t("RESPONSE_MESSAGE403"));
          }
          else if (err?.response?.status === 401) {
            toast.error(t("RESPONSE_MESSAGE401"));
          } else if (err?.response?.status === 400) {
            toast.error(t("RESPONSE_MESSAGE400"));
          } else if (err?.response?.status === 409) {
            toast.error(t("RESPONSE_MESSAGE409"));
          } else if (err?.response?.status === 404) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            if (String(err?.response?.data?.error)?.includes("ER_DUP_ENTRY")) {
              toast.error(t("ER_DUP_ENTRY"));
            } else if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
              toast.error(t("RESPONSE_MESSAGE404"));
            } else {
              toast.error(t("RESPONSE_MESSAGE500"));
            }
          }
        }
      }
    }
  };

  const handleDeleteOnClick = async function (e) {
    e.preventDefault();
    try {
      const res = await topicAPI.deleteById(id);
      if (res.status === 200) {
        toast.success(t("admin.common.DELETED"));
        history.push(`${RoutesString.ADMIN_TOPIC_LIST}`);
      }
    } catch (err) {
      if (err.response) {
        if (err?.response?.status === 403 || String(err?.response?.message) === 'NOT_OWNER') {
          toast.error(t("RESPONSE_MESSAGE403"));
        }
        else if (err?.response?.status === 401) {
          toast.error(t("RESPONSE_MESSAGE401"));
        } else if (err?.response?.status === 400) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE400"));
        } else if (err?.response?.status === 409) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE409"));
        } else if (err?.response?.status === 404) {
          toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE404"));
        } else {
          if (String(err?.response?.data?.error)?.includes("ID_NOT_FOUND")) {
            toast.error(t("RESPONSE_MESSAGE404"));
          } else {
            toast.error(t("DELETE_ERROR") + ": " + t("RESPONSE_MESSAGE500"));
          }
        }
      }
    }
  };

  return (
    <div className="page">
      {isLoading && <LoadingBar />}

      <Breadcrumbs
        currentItem={
          isUpdatePage
            ? t("admin.topic_edit.UPDATE_TOPIC")
            : t("admin.topic_edit.ADD_TOPIC")
        }
        items={[
          {
            href: RoutesString.ADMIN_TOPIC_LIST,
            title: t("admin.topic_list.TOPIC_LIST"),
          },
        ]}
      />

      <PageHeader
        titleHeader={
          isUpdatePage
            ? t("admin.topic_edit.UPDATE_TOPIC")
            : t("admin.topic_edit.ADD_TOPIC")
        }
      />

      <div className="page-content">
        <Formik
          initialValues={initialValues}
          enableReinitialize={true}
          validationSchema={Yup.object().shape({
            ma: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
            ten: Yup.string().required(i18n.t("REQUIRED_FIELD_WARNING")),
            tuan: Yup.number()
              .required(i18n.t("REQUIRED_FIELD_WARNING"))
              // .integer("Week must be a number")
              .min(0, i18n.t("admin.POSITIVE_WARNING")),
          })}
        >
          {({
            values,
            errors,
            dirty,
            isValid,
            handleChange,
            handleReset,
            setFieldValue,
          }) => (
            <Form>
              <Notification
                color={notification.color}
                isOpen={notification.isOpen}
                messages={notification.messages}
              />
              <FormGroup className="row">
                <Label for="ma" className="col-lg-3">
                  {t("admin.topic_edit.CODE")} <AsteriskIcon />
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaCode />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="text"
                      name="ma"
                      placeholder={t("admin.topic_edit.CODE_PLACEHOLDER")}
                      value={values.ma}
                      onChange={handleChange}
                      invalid={errors.ma}
                      readOnly={isUpdatePage}
                    />
                  </InputGroup>
                  {errors.ma && <FormText color="danger">{errors.ma}</FormText>}
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="ten" className="col-lg-3">
                  {t("admin.topic_edit.NAME")} <AsteriskIcon />
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaTextWidth />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="text"
                      name="ten"
                      placeholder={t("admin.topic_edit.NAME_PLACEHOLDER")}
                      value={values.ten}
                      onChange={handleChange}
                      invalid={errors.ten}
                    />
                  </InputGroup>
                  {errors.ten && (
                    <FormText color="danger">{errors.ten}</FormText>
                  )}
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="tuan" className="col-lg-3">
                  {t("admin.topic_edit.WEEK")} <AsteriskIcon />
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaCalendar />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="number"
                      name="tuan"
                      placeholder={t("admin.topic_edit.WEEK_PLACEHOLDER")}
                      value={values.tuan}
                      onChange={handleChange}
                      invalid={errors.tuan}
                    />
                  </InputGroup>
                  {errors.tuan && (
                    <FormText color="danger">{errors.tuan}</FormText>
                  )}
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="idSyllabus" className="col-lg-3">
                  {t("Syllabus")}
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaBook />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="select"
                      name="idSyllabus"
                      value={values.idSyllabus}
                      onChange={handleChange}
                    >
                      {syllabuses &&
                        syllabuses.map((item, index) => {
                          return (
                            <option key={index} value={item.id}>
                              {`${item.monHoc.ma} - ${item.heDaoTao.ten} - ${item.namHoc.ten}`}
                            </option>
                          );
                        })}
                    </Input>
                  </InputGroup>
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="idLKHGD" className="col-lg-3">
                  {t("admin.topic_edit.TEACHING_PLAN_TYPE")}
                </Label>
                <div className="col-lg-6">
                  <InputGroup>
                    <InputGroupAddon addonType="prepend">
                      <InputGroupText>
                        <FaColumns />
                      </InputGroupText>
                    </InputGroupAddon>
                    <Input
                      type="select"
                      name="idLKHGD"
                      value={values.idLKHGD}
                      onChange={handleChange}
                    >
                      {teachingPlanTypes &&
                        teachingPlanTypes.map((item, index) => {
                          return (
                            <option key={index} value={item.id}>
                              {item.ten}
                            </option>
                          );
                        })}
                    </Input>
                  </InputGroup>
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="hoatDongDayHoc" className="col-lg-3">
                  {t("admin.topic_edit.TEACHING_ACTIVITY")}
                </Label>
                <div className="col-lg-6">
                  <Select
                    value={values.hoatDongDayHoc}
                    onChange={(e) =>
                      setFieldValue("hoatDongDayHoc", Array.isArray(e) ? e : [])
                    }
                    isMulti
                    closeMenuOnSelect={false}
                    options={teachingActivities}
                    placeholder={t(
                      "admin.topic_edit.TEACHING_ACTIVITY_PLACEHOLDER"
                    )}
                  />
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="hoatDanhGia" className="col-lg-3">
                  {t("admin.topic_edit.EVALUATION_ACTIVITY")}
                </Label>
                <div className="col-lg-6">
                  <Select
                    value={values.hoatDongDanhGia}
                    onChange={(e) =>
                      setFieldValue(
                        "hoatDongDanhGia",
                        Array.isArray(e) ? e : []
                      )
                    }
                    isMulti
                    closeMenuOnSelect={false}
                    options={evaluationActivities}
                    placeholder={t(
                      "admin.topic_edit.EVALUATION_ACTIVITY_PLACEHOLDER"
                    )}
                  />
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup className="row">
                <Label for="chuanDauRaMonHoc" className="col-lg-3">
                  {t("admin.topic_edit.SUBJECT_OUTPUT_STANDARD")}
                </Label>
                <div className="col-lg-6">
                  <Select
                    value={values.chuanDauRaMonHoc}
                    onChange={(e) =>
                      setFieldValue(
                        "chuanDauRaMonHoc",
                        Array.isArray(e) ? e : []
                      )
                    }
                    isMulti
                    closeMenuOnSelect={false}
                    options={subjectOutputStandards}
                    placeholder={t(
                      "admin.topic_edit.SUBJECT_OUTPUT_STANDARD_PLACEHOLDER"
                    )}
                  />
                </div>
              </FormGroup>{" "}
              <br />
              <FormGroup>
                <Button
                  color="success"
                  disabled={isUpdatePage ? !isValid : !(isValid && dirty)}
                  onClick={() =>
                    handleEditOnClick(
                      values,
                      isUpdatePage ? isValid : isValid && dirty
                    )
                  }
                >
                  {isUpdatePage ? (
                    <>
                      <FaExchangeAlt /> {t("admin.common.UPDATE")}
                    </>
                  ) : (
                    <>
                      <FaPlus /> {t("admin.common.ADD")}
                    </>
                  )}
                </Button>
                &emsp;
                {isUpdatePage && (
                  <>
                    <Button onClick={handleReset}>
                      <FaSync /> {t("admin.common.REFRESH")}
                    </Button>
                    &emsp;
                    <Button color="danger" onClick={handleDeleteOnClick}>
                      <FaTrash /> {t("admin.common.DELETE")}
                    </Button>
                  </>
                )}
              </FormGroup>
            </Form>
          )}
        </Formik>
      </div>
    </div>
  );
}
